import Vue from 'vue'
import Vuex from 'vuex'
import HomeView from '../views/HomeView.vue'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    count: 0,
    headerColorHex: '',
    lightThreshold: 0.5,
    routes: [
      {
        path: '/',
        name: 'viewer',
        component: HomeView,
        icon: 'mdi-home',
      },
      {
        path: '/composer',
        name: 'composer',
        component: () => import(/* webpackChunkName: "about" */ '../views/ComposerView.vue'),
        icon: 'mdi-palette',
      }
    ]
  },
  mutations: {
    increment (state) {
      state.count++
    },
    setHeaderColorHex(state, hex) {
      state.headerColorHex = hex;
    }
  },
  actions: {
    setHeaderColorHex({ commit }, hex) {
      commit('setHeaderColorHex', hex)
    }
  }
})

export default store;
