<template>
  <v-app>
    <v-navigation-drawer
      app
      v-model="drawer"
      color="primary darken-2"
    >
      <div class="text-left">
        <v-btn
          style="margin: 4px; width: 56px; height: 56px;"
          @click="drawer = !drawer"
          color="white"
          icon
        >
          <v-icon >mdi-close</v-icon>
        </v-btn>
      </div>
      <slot name="left-drawer">
        <v-list :dark="dark" class="pt-0">
          <v-list-item
            :to="{ name: route.name }"
            v-for="route in routes" :key="route.name"
          >
            <v-list-item-avatar>
              <v-icon>{{ route.icon }}</v-icon>
            </v-list-item-avatar>
            <v-list-item-content>{{ capitalize(route.name) }}</v-list-item-content>
          </v-list-item>
        </v-list>
      </slot>
    </v-navigation-drawer>
    <v-app-bar app class="primary darken-2" :dark="dark" :elevation="0">
      <v-app-bar-nav-icon @click="drawer = !drawer"></v-app-bar-nav-icon>
      <slot name="app-bar-left"></slot>
      <v-spacer></v-spacer>
      <span class="app-name">3Palatte</span>
      <v-spacer></v-spacer>
      <slot name="app-bar-right"></slot>
    </v-app-bar>

    <!-- Sizes your content based upon application components -->
    <v-main class="secondary">
      <slot name="main"></slot>
    </v-main>

    <v-footer app class="primary darken-2">
      <slot name="footer"></slot>
    </v-footer>
  </v-app>
</template>

<script>
import { mapState } from 'vuex';
import onecolor from 'onecolor';
import { capitalize } from '../lib/utils';

export default {
  computed: {
    ...mapState([
      'headerColorHex',
      'lightThreshold',
      'routes',
    ]),
    dark() {
      return onecolor(this.headerColorHex || 'f00').lightness() < this.lightThreshold;
    }
  },
  data: () => ({
    drawer: false,
  }),
  methods: {
    onecolor,
    capitalize
  }
}
</script>
